import { DownOutlined } from "@ant-design/icons";
import { Dropdown, Button, Space, Menu } from "antd";
import { BgNodes } from "../..";
import { BG_POSITION } from "../../constants";

interface Props {
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
}

export default function BgNode({ setBgNodes }: Props) {
  const handleAddBgBlock = (e: any) => {
    let style: React.CSSProperties = {
      width: "calc(100% - 1rem)",
      height: "11.5rem",
    };
    switch (e.key) {
      case BG_POSITION.LEFT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
        };
        break;
      case BG_POSITION.BOTTOM:
        style = {
          ...style,
          bottom: "0.5rem",
        };
        break;
      case BG_POSITION.RIGHT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
          right: "0.5rem",
        };
        break;
    }

    setBgNodes((v) => {
      return [...v, style];
    });
  };

  const bgBlocks = (
    <Menu
      onClick={handleAddBgBlock}
      items={[
        {
          label: "",
          key: BG_POSITION.TOP,
        },
        {
          label: "",
          key: BG_POSITION.LEFT,
        },
        {
          label: "",
          key: BG_POSITION.RIGHT,
        },
        {
          label: "",
          key: BG_POSITION.BOTTOM,
        },
      ]}
    />
  );

  return (
    <Dropdown overlay={bgBlocks}>
      <Button type="text">
        <Space>
          
          <DownOutlined className="leading-6" />
        </Space>
      </Button>
    </Dropdown>
  );
}
