import React, { useEffect, useState } from "react";
import { Button, Dropdown, Menu, Space } from "antd";
import { DownOutlined } from "@ant-design/icons";
// import html2pdf from "html2pdf.js";
import { Link, useLocation } from "react-router-dom";

import DividerModal from "../divider";
import TagModal from "../tag";

import Themes from "../themes";
import Icons from "../icons";
import ToGithub from "../toGithub";

import Logo from "../../../../assets/logo.png";
import { Data, BgNodes, Layouts } from "../../index";

// import { layout1, blockData1 } from "../../tpl/tpl";
// import { layout2, blockData2, bgNodes2, theme2 } from "../../tpl/tpl2";
// import { layout3, blockData3, theme3 } from "../../tpl/tpl3";
// import { layouts4, bgNodes4, theme4 } from "../../tpl/tpl4";
import {
  TYPE,
  CONSTANTS,
  JSON2JSX_ELEMENT,
  DEFAULT_THEME,
} from "../../constants";

import print from "../../../../utils/print";
import { json2jsx } from "../../../../utils";
import BgNode from "../bgNode";
interface Props {
  addBlock: ({ type, text, layout }: Data) => void;
  setTheme: React.Dispatch<React.SetStateAction<string>>;
  setLayouts: React.Dispatch<React.SetStateAction<Layouts[]>>;
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
  setPrintNodes: React.Dispatch<React.SetStateAction<string[]>>;
  setTotal: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  printNodes: string[];
  page: number;
}

export default function Header({
  addBlock,
  setTheme,
  setLayouts,
  setBgNodes,
  setPrintNodes,
  setTotal,
  setPage,
  printNodes,
  page,
}: Props) {
  const [dividerModalVisible, setDividerModalVisible] = useState(false);
  const [tagModalVisible, setTagModalVisible] = useState(false);

  const [toGithubVisible, setToGithubVisible] = useState(false);

  const location = useLocation();

  useEffect(() => {
    if (location.search) {
      handleChangeTpl({ key: location.search.substring(1) });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.search]);

  const handleChangeTpl = async (e: any) => {
    let layouts: Layouts[] = [],
      theme: string = DEFAULT_THEME;
    let bgNodes: React.CSSProperties[] = [];
    switch (Number(e.key)) {
      case 1:
        const { layouts1, theme1 } = await import("../../tpl/tpl1");
        layouts = layouts1;
        theme = theme1;
        break;

      case 2:
        const { layouts2, bgNodes2, theme2 } = await import("../../tpl/tpl2");
        layouts = layouts2;
        bgNodes = bgNodes2;
        theme = theme2;
        break;
      case 3:
        const { layouts3, theme3 } = await import("../../tpl/tpl3");
        layouts = layouts3;
        theme = theme3;
        break;
      case 4:
        const { layouts4, bgNodes4, theme4 } = await import("../../tpl/tpl4");
        layouts = layouts4;
        bgNodes = bgNodes4;
        theme = theme4;
        break;
      case 5:
        const { layouts5, bgNodes5, theme5 } = await import("../../tpl/tpl5");
        layouts = layouts5;
        bgNodes = bgNodes5;
        theme = theme5;
        break;
      case 6:
        const { layouts6, bgNodes6, theme6 } = await import("../../tpl/tpl6");
        layouts = layouts6;
        bgNodes = bgNodes6;
        theme = theme6;
        break;
      case 0:
        layouts = [
          {
            block: {},
            layout: [],
          },
        ];
        bgNodes = [];
        break;
      case -1:
        const { layoutsText } = await import("../../tpl/tpl-text");
        layouts = layoutsText;
    }
    layouts = layouts.map((item) => {
      Object.keys(item.block).forEach((subItem) => {
        const key = subItem.split("-")[0];

        if (JSON2JSX_ELEMENT.includes(key)) {
          item.block[subItem].text = json2jsx(
            item.block[subItem].text as JSX.Element
          );
        }
      });
      return item;
    });
    setLayouts(layouts);
    setTheme(theme);
    setBgNodes(bgNodes);
    setTotal(layouts.length);
    setPage(0);
  };

  const handleAddBlock = (e: any) => {
    const type = e.key;
    if (type === TYPE.DIVIDER) {
      setDividerModalVisible(true);

      return;
    }
    if (type === TYPE.TAG) {
      setTagModalVisible(true);

      return;
    }
    addBlock({
      type,
      text: "",
      layout: {
        w: 6,
        h: 2,
      },
    });
  };

  const handleUploadPicture = () => {
    var reads = new FileReader();
    const input = document.getElementById("uploadImage") as HTMLInputElement;
    const f = input.files?.[0] as File;

    reads.readAsDataURL(f);
    reads.onload = (e) => {
      addBlock({
        type: "img",
        text: {
          type: "img",
          props: {
            className: "h-full",
            src: e.target?.result,
            alt: "",
          },
        },
        layout: {
          i: `${CONSTANTS.IMG}-${Date.now()}`,
          w: 6,
          h: 6,
        },
      });
    };
  };

  function handleExport() {
    const finalPrintArr = [...printNodes];
    finalPrintArr[page] = document.querySelector(
      `#${CONSTANTS.PREVIEW_BOX}${page}`
    )!.innerHTML;

    setPrintNodes(finalPrintArr);

    print(finalPrintArr);
    setToGithubVisible(true);
  }

  const handleIconClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const icon = (e.target as HTMLUListElement).title;
    addBlock({
      type: "icon",
      text: {
        type: "i",
        props: {
          className: `iconfont ${icon}`,
        },
      },
      layout: {
        i: `${CONSTANTS.ICON}-${Date.now()}`,
        w: 2,
        h: 2,
      },
    });
  };

  const handleThemeClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const color = (e.target as HTMLUListElement).title;
    setTheme(color);
  };

  const tpls = (
    <Menu
      onClick={handleChangeTpl}
      items={[
        {
          label: "1",
          key: 1,
        },
        {
          label: "2",
          key: 2,
        },
        {
          label: "3",
          key: 3,
        },
        {
          label: "4",
          key: 4,
        },
        {
          label: "5",
          key: 5,
        },
        {
          label: "6",
          key: 6,
        },
        {
          label: "",
          key: 0,
        },
        {
          label: "",
          key: -1,
        },
      ]}
    />
  );

  const blocks = (
    <Menu
      onClick={handleAddBlock}
      items={[
        {
          label: "",
          key: TYPE.P,
        },
        {
          label: "------",
          key: TYPE.DIVIDER,
        },
        {
          label: "Tag",
          key: TYPE.TAG,
        },
      ]}
    />
  );

  return (
    <header className="fixed w-full bg-white z-10 py-2 px-10 shadow-xl flex justify-between">
      <h3 className="w-2/12">
        <img className="h-8" src={Logo} alt="" />
      </h3>
      <div className="mx-4 cursor-pointer w-14 leading-8 font-medium text-xl hover:blue-300">
        <Link to="/"></Link>
      </div>
      <div className="w-full">
        <Dropdown overlay={tpls}>
          <Button type="text">
            <Space>
              
              <DownOutlined className="leading-6" />
            </Space>
          </Button>
        </Dropdown>
        <Dropdown overlay={blocks}>
          <Button type="text">
            <Space>
              
              <DownOutlined className="leading-6" />
            </Space>
          </Button>
        </Dropdown>
        <BgNode setBgNodes={setBgNodes} />
        <Icons handleIconClick={handleIconClick} />
        <Button type="text">
          <input
            className="hidden"
            accept="image/*"
            id="uploadImage"
            onChange={handleUploadPicture}
            type="file"
          />
          <label htmlFor="uploadImage"></label>
        </Button>
        <Themes handleThemeClick={handleThemeClick} />
      </div>
      <Button type="dashed" onClick={handleExport}>
        PDF
      </Button>
      <i
        title="icon-github"
        className="iconfont icon-github rounded-full h-10 w-10 pt-1.5 pl-3 cursor-pointer"
        onClick={() =>
          window.open("https://github.com/MasonEast/MasonEast.github.io")
        }
      />
      <DividerModal
        visible={dividerModalVisible}
        setVisible={setDividerModalVisible}
        addBlock={addBlock}
      />
      <TagModal
        visible={tagModalVisible}
        setVisible={setTagModalVisible}
        addBlock={addBlock}
      />
      <ToGithub visible={toGithubVisible} setVisible={setToGithubVisible} />
    </header>
  );
}
