import React, { useEffect, useState } from "react";
import { Tooltip } from "antd";
import {
  CloseOutlined,
  LeftSquareOutlined,
  RightSquareOutlined,
  PlusSquareOutlined,
} from "@ant-design/icons";
import { Layout } from "react-grid-layout";
import _ from "lodash";

import { CONSTANTS, DIVIDER_TYPES_FN, TAG_TYPES_FN } from "./constants";
import Header from "./components/header";

// import { bgNodes2, theme2 } from "./tpl/tpl2";
import MyGridLayout from "./components/gridLayout";

export type MyElement = {
  type: string;
  props: Record<string, any>;
  key?: string | number;
};

export type Data = {
  text: MyElement | string | JSX.Element;
  type: string;
  title?: string;
  theme?: string;
  layout?: Partial<Layout>;
  style?: React.CSSProperties;
  reload?: boolean;
};

export type BgNodes = React.CSSProperties;
export interface EnhanceLayout extends Layout {
  data: Data;
}

export type Layouts = {
  block: Record<string, Data>;
  layout: Layout[];
};

function App() {
  const [page, setPage] = useState(0);
  const [total, setTotal] = useState(1);
  const [layouts, setLayouts] = useState<Layouts[]>([
    {
      block: {},
      layout: [],
    },
  ]);
  const [theme, setTheme] = useState("");
  const [bgNodes, setBgNodes] = useState<BgNodes[]>([]);
  const [printNodes, setPrintNodes] = useState<string[]>([]);

  useEffect(() => {
    setLayouts((data) => {
      const arr = [...data];
      Object.keys(data[page].block).forEach((item) => {
        const nodeType = item.split("-")[0];
        const block = layouts[page].block[item];
        switch (nodeType) {
          // dividertag
          case CONSTANTS.DIVIDER:
          case CONSTANTS.TAG: {
            const { type: bType, title } = block;

            const fn =
              nodeType === CONSTANTS.TAG ? TAG_TYPES_FN : DIVIDER_TYPES_FN;

            const { type, props, key } = fn(bType, title, theme);

            arr[page].block[item] = {
              ...block,
              text: {
                type,
                props,
                key,
              },
              theme,
            };
            break;
          }
        }
      });

      return arr;
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [theme, page]);

  const addBlock = ({
    type = "p",
    text = "",
    title,
    theme,
    layout = {},
    style = {},
  }: Data) => {
    const key = layout.i || "p-" + Date.now() + layouts[page].layout.length;
    // 
    if (!layout.y && layout.y !== 0) {
      const lastY = layouts[page].layout.reduce(
        (pre, cur) => Math.max(pre, cur.y),
        0
      );
      layout.y = lastY;
      layout.x = 0;
    }

    layout.i = key;

    setLayouts((layouts) => {
      const arr = _.cloneDeep(layouts);

      arr[page].layout.push(layout as Layout);
      arr[page].block[key] = {
        type,
        text,
        title,
        theme,
        style,
      };

      return arr;
    });
  };

  const handleDeleteBgNode = (i: number) => {
    setBgNodes((n) => {
      const arr = [...n];
      arr.splice(i, 1);
      return arr;
    });
  };

  const handlePre = () => {
    setPrintNodes((v) => {
      const arr = [...v];

      arr[page] = document.querySelector(
        `#${CONSTANTS.PREVIEW_BOX}${page}`
      )!.innerHTML;
      return arr;
    });
    setPage(page - 1);
  };

  const handleNext = () => {
    const node = document.querySelector(`#${CONSTANTS.PREVIEW_BOX}${page}`)!;

    setPrintNodes((v) => {
      const arr = [...v];
      arr[page] = node.innerHTML;
      return arr;
    });
    setPage(page + 1);
    if (page + 1 === total) {
      setLayouts([
        ...layouts,
        {
          layout: [],
          block: {},
        },
      ]);
      setTotal(total + 1);
    }
  };

  // console.log(JSON.stringify(layouts), "---ab");

  return (
    <div className="App">
      <Header
        addBlock={addBlock}
        setTheme={setTheme}
        setLayouts={setLayouts}
        setBgNodes={setBgNodes}
        printNodes={printNodes}
        setPrintNodes={setPrintNodes}
        setTotal={setTotal}
        setPage={setPage}
        page={page}
      />
      <div className="flex justify-between bg-gray-300 py-20">
        {/* edit */}
        <div className="w-6/12 flex justify-center relative">
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            {bgNodes.map((item, i) => (
              <div
                style={{
                  position: "absolute",
                  backgroundColor: theme,
                  ...item,
                }}
                key={i}
              >
                <CloseOutlined
                  className="absolute top-0 right-0 text-localxs cursor-pointer z-10"
                  onClick={() => handleDeleteBgNode(i)}
                />
              </div>
            ))}
            {layouts.map((item, i) => (
              <MyGridLayout
                key={i}
                data={item}
                addBlock={addBlock}
                setLayouts={setLayouts}
                page={page}
                index={i}
              />
            ))}
          </div>
          <Tooltip title="">
            <LeftSquareOutlined
              className="absolute text-white left-12"
              style={{
                fontSize: 60,
                top: "40%",
                display: page === 0 ? "none" : "block",
              }}
              onClick={handlePre}
            />
          </Tooltip>

          {page + 1 === layouts.length ? (
            <Tooltip title="">
              <PlusSquareOutlined
                className="absolute text-white right-12"
                style={{
                  fontSize: 60,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          ) : (
            <Tooltip title="">
              <RightSquareOutlined
                className="absolute text-white right-12"
                style={{
                  fontSize: 60,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          )}
        </div>
        {/* show */}
        <div
          id={`${CONSTANTS.PREVIEW_BOX}${page}`}
          className="w-6/12  flex justify-center"
        >
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            <div>
              {bgNodes.map((item) => (
                <div
                  style={{
                    position: "absolute",
                    backgroundColor: theme,
                    ...item,
                  }}
                ></div>
              ))}
              {layouts.map((item, i) => (
                <MyGridLayout
                  key={`show${i}`}
                  data={item}
                  addBlock={addBlock}
                  setLayouts={setLayouts}
                  page={page}
                  index={i}
                  isEdit={false}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
