import { Modal } from "antd";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function ToGithub({ visible, setVisible }: Props) {
  const handleOk = () => {
    setVisible(false);
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title=""
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      okText={""}
      cancelText={""}
    >
      <p>star(:</p>
      <a
        className="pointer text-blue-300 mt-4"
        target={"__blank"}
        href="https://github.com/MasonEast/MasonEast.github.io"
      >
        Github
      </a>
    </Modal>
  );
}
